import { createRoot } from 'react-dom/client';
import './style.scss';
import Style from './Components/Common/Style';
import VideoPlaylistGallery from './Components/Common/VideoPlaylistGallery/VideoPlaylistGallery';

document.addEventListener('DOMContentLoaded', () => {
	const blockNameEls = document.querySelectorAll('.wp-block-vpg-video-playlist-gallery');
	blockNameEls.forEach(blockNameEl => {
		const attributes = JSON.parse(blockNameEl.dataset.attributes);

		createRoot(blockNameEl).render(<>
			<Style attributes={attributes} id={blockNameEl.id} />

			<VideoPlaylistGallery attributes={attributes} />

		</>);

		blockNameEl?.removeAttribute('data-attributes');
	});
});